.button-menu {
background-color: transparent;
border: none;
position: absolute;
top: 6px;
bottom: 6px;
right: 0;
cursor: pointer;

&:hover, &:focus {
fill: $accent;
}
@media screen and (min-width:$tablet) {
display: none;
}
}

.button-menu__icons.button-menu__hamburger,
.button-menu__icons.button-menu__cros {
fill: $primary-text;
}

.button-menu__hamburger,
.button-menu__cros {
cursor: pointer;

&:hover, &:focus {
fill: $accent;
}
}

.button-menu .button-menu__cros {
display: block;
}

.button-menu.is-open .button-menu__cros {
display: none;
}

.button-menu .button-menu__hamburger {
display: none;
}

.button-menu.is-open .button-menu__hamburger {
display: block;
}