*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0, 0, 0, 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

@font-face {
  font-family: Manrope;
  src: local(Manrope), url("Manrope-Bold.33b2b0ee.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: Manrope;
  src: local(Manrope), url("Manrope-Medium.414d629b.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: Manrope;
  src: local(Manrope), url("Manrope-Regular.a0d78fb1.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: Poppins;
  src: local(Poppins), url("Poppins-Regular.d5d72893.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: Roboto;
  src: local(Roboto), url("Roboto-Regular.ca197847.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}

body {
  color: #111321;
  background-color: #f4f4f4;
  margin: 0;
  position: relative;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

li {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

section {
  position: relative;
}

.header {
  border-bottom: 1px solid rgba(0, 0, 0, .2);
  padding: 16px 0;
}

.header__nav {
  align-items: center;
  padding-left: 16px;
  padding-right: 16px;
  display: flex;
  position: relative;
}

.header__logo {
  color: #111321;
  width: 63px;
  height: 33px;
  font-family: Manrope;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.357;
  display: block;
}

@media screen and (min-width: 768px) {
  .header__logo {
    width: 84px;
    height: 44px;
    margin-right: 48px;
    font-size: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .header__logo {
    width: 111px;
    height: 57px;
    margin-right: 102px;
    font-size: 42px;
  }
}

@media screen and (max-width: 768px) {
  .menu__container {
    width: 100%;
    z-index: 1;
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
    background-color: #fff;
    padding-bottom: 18px;
    padding-left: 16px;
    transition: transform .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
    display: block;
    position: absolute;
    top: 98px;
    transform: scaleY(0);
  }

  .menu__container.is-open {
    pointer-events: auto;
    opacity: 1;
    visibility: visible;
    transform: scaleY(1);
  }
}

@media screen and (min-width: 768px) {
  .menu__container {
    align-items: center;
    display: flex;
  }
}

.site-nav {
  display: flex;
}

@media screen and (max-width: 768px) {
  .site-nav {
    flex-direction: column;
    gap: 30px;
    margin-bottom: 248px;
  }
}

@media screen and (min-width: 768px) {
  .site-nav {
    gap: 44px;
    margin-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .site-nav {
    gap: 69px;
    margin-right: 147px;
  }
}

.header__link {
  color: #111321;
  font-family: Manrope;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.38;
}

@media screen and (max-width: 1280px) {
  .header__link {
    font-size: 16px;
  }
}

.header__link--active {
  padding-bottom: 5px;
  position: relative;
}

@media screen and (min-width: 768px) {
  .header__link--active:after {
    content: "";
    width: 100%;
    height: 1px;
    background-color: #4440f6;
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
  }
}

.search-form {
  position: relative;
}

.search-input {
  height: 31px;
  color: #111321;
  width: 173px;
  border: 1px solid #111321;
  border-radius: 20px;
  padding: 5px 0 5px 48px;
}

@media screen and (min-width: 768px) {
  .search-input {
    width: 213px;
  }
}

@media screen and (min-width: 1280px) {
  .search-input {
    width: 288px;
  }
}

.search-button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  bottom: 50%;
  left: 12px;
}

.icon-search {
  fill: #111321;
}

.button-menu {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: absolute;
  top: 6px;
  bottom: 6px;
  right: 0;
}

.button-menu:hover, .button-menu:focus {
  fill: #4440f6;
}

@media screen and (min-width: 768px) {
  .button-menu {
    display: none;
  }
}

.button-menu__icons.button-menu__hamburger, .button-menu__icons.button-menu__cros {
  fill: #111321;
}

.button-menu__hamburger, .button-menu__cros {
  cursor: pointer;
}

.button-menu__hamburger:hover, .button-menu__hamburger:focus, .button-menu__cros:hover, .button-menu__cros:focus {
  fill: #4440f6;
}

.button-menu .button-menu__cros {
  display: block;
}

.button-menu.is-open .button-menu__cros, .button-menu .button-menu__hamburger {
  display: none;
}

.button-menu.is-open .button-menu__hamburger {
  display: block;
}

