.news-card {
  width: 288px;
  height: 630px;
  border-radius: 4px;
  list-style: none;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

@media screen and (max-width: 767px) {
  .news-card:not(:last-child) {
    margin-bottom: 28px;
  }
}

@media screen and (min-width: 768px) {
  .news-card {
    width: calc(50% - 15px);
    height: 675px;
  }
}

@media screen and (min-width: 1280px) {
  .news-card {
    width: calc(33.3333% - 21.3333px);
  }
}

.news-card:hover, .news-card:focus {
  box-shadow: 0 3px 15px rgba(0, 0, 0, .2);
}

.news-card__darkend[data-read="false"] {
  display: none;
}

.news-card__darkend[data-read="true"] {
  opacity: .4;
  max-width: 100%;
  height: 100%;
  z-index: 1;
  background-color: #f4f4f4;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.news-card__image {
  width: 288px;
  height: 395px;
  margin-bottom: 20px;
  position: relative;
}

@media screen and (min-width: 768px) {
  .news-card__image {
    width: 353px;
    height: 395px;
  }
}

@media screen and (min-width: 1280px) {
  .news-card__image {
    width: 395px;
    height: 395px;
  }
}

.news-card__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 4px;
}

.news-card__category {
  color: #fff;
  background-color: rgba(68, 64, 246, .7);
  padding: 4px 8px;
  font-family: Manrope;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  position: absolute;
  top: 40px;
  left: 0;
}

.news-card__status[data-read="false"] {
  display: none;
}

.news-card__status[data-read="true"] {
  vertical-align: middle;
  color: #00dd73;
  fill: currentColor;
  font-family: Manrope;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.38;
  display: flex;
  position: absolute;
  top: 14px;
  right: 14px;
}

@media screen and (min-width: 768px) {
  .news-card__status[data-read="true"] {
    top: 20px;
    right: 21px;
  }
}

.news-card__icon-tick {
  margin-left: 4px;
}

.news-card__favorite {
  text-align: center;
  color: #111321;
  z-index: 2;
  cursor: pointer;
  background: #fff;
  border: none;
  border-radius: 20px;
  padding: 6px 12px;
  font-family: Manrope;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  transition: border .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  bottom: 12px;
  right: 8px;
}

@media screen and (min-width: 768px) {
  .news-card__favorite {
    bottom: 12px;
    right: 12px;
  }
}

.news-card__favorite:hover, .news-card__favorite:focus {
  border: 1px solid rgba(255, 255, 255, .5);
}

.news-card__icon[data-favorite="false"] {
  fill: #4440f6;
  margin-left: 4px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  transform: scale(1);
}

.news-card__icon[data-favorite="false"]:hover, .news-card__icon[data-favorite="false"]:focus {
  transform: scale(1.3);
}

.news-card__icon[data-favorite="true"], .news-card__icon-full[data-favorite="false"] {
  display: none;
}

.news-card__icon-full[data-favorite="true"] {
  fill: #4440f6;
  margin-left: 4px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  transform: scale(1);
}

.news-card__icon-full[data-favorite="true"]:hover, .news-card__icon-full[data-favorite="true"]:focus {
  transform: scale(1.3);
}

